export default function(timestamp){
	let anchorParams = getAnchorParams(new Date())
	return getWxTimeMemo(new Date(timestamp),anchorParams) 
}

// 
// let currentTime = strToDate('2022-09-08 23:09:15')
// 3
// 
function getWxTimeMemo(date, anchorParams) {
  let pointParams = parseTimeAllParams(date)
  let anchorTimestamp = anchorParams.timestamp
  let pointTimestamp = pointParams.timestamp
  let anchorYear = anchorParams.y
  let pointYear = pointParams.y

  //  
  //  /  [0-5/  6-11 /  12 - 23] 6:00
  // 7  
  //  xxx
  //  xxxx xxx
  let condition = [
	{ timestamp: anchorTimestamp + 3600 * 1000 * 12, format: '{h}:{i}' }, // 
	{ timestamp: anchorTimestamp + 3600 * 1000 * 6, format: '{h}:{i}' }, // 
	{ timestamp: anchorTimestamp, format: '{h}:{i}' }, // 
	{ timestamp: anchorTimestamp - 3600 * 1000 * 12, format: ' {h}:{i}' }, // 
	{ timestamp: anchorTimestamp - 3600 * 1000 * 18, format: ' {h}:{i}' }, // 
	{ timestamp: anchorTimestamp - 3600 * 1000 * 24, format: ' {h}:{i}' }, // 
	{ timestamp: anchorTimestamp - 3600 * 1000 * 24 * 6, format: '{a} {h}:{i}' }, // 7
	{ timestamp: pointYear - anchorYear === 0, format: '{m}{d} {h}:{i}' }, // (0)
	{ timestamp: pointYear - anchorYear < 0, format: '{y}{m}{d} {h}:{i}' } // 
  ]
  for (let i = 0; i < condition.length; i++) {
	if (typeof condition[i].timestamp === 'number') {
	  if (pointTimestamp >= condition[i].timestamp) {
		return parseTimeAllParams(pointParams.time, condition[i].format).strTime
	  }
	} else {
	  if (condition[i].timestamp) {
		return parseTimeAllParams(pointParams.time, condition[i].format).strTime
	  }
	}
  }
  return parseTimeAllParams(pointParams.time, '{y}{m}{d}').strTime
}

function parseTimeAllParams(time, cFormat) {
  if (arguments.length === 0 || !time) {
	return null
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
	date = time
  } else {
	if ((typeof time === 'string')) {
	  if ((/^[0-9]+$/.test(time))) {
		// support "1548221490638"
		time = parseInt(time)
	  } else {
		// support safari
		time = time.replace(new RegExp(/-/gm), '/')
	  }
	}

	if ((typeof time === 'number') && (time.toString().length === 10)) {
	  time = time * 1000
	}
	date = new Date(time)
  }
  const formatObj = {
	y: date.getFullYear(),
	m: date.getMonth() + 1,
	d: date.getDate(),
	h: date.getHours(),
	i: date.getMinutes(),
	s: date.getSeconds(),
	a: date.getDay()
  }
  const time_str = format.replace(/{([ymdhisa])+}/g, (result, key) => {
	const value = formatObj[key]
	// Note: getDay() returns 0 on Sunday
	if (key === 'a') { return ['', '', '', '', '', '', ''][value ] }
	return value.toString().padStart(2, '0')
  })
  return {
	strTime: time_str, ...formatObj, a: ['', '', '', '', '', '', ''][date.getDay()], timestamp: time.getTime(), time: date
  }
}
function strToDate(str, pattern = 'yyyy-MM-dd hh:mm:ss') {
  if (str && typeof str === 'string') {
	let formatIndex = [
	  /[y]+/.exec(pattern),
	  /[M]+/.exec(pattern),
	  /[d]+/.exec(pattern),
	  /[h]+/.exec(pattern),
	  /[m]+/.exec(pattern),
	  /[s]+/.exec(pattern)
	]
	let datePart = [0, 1, 1, 0, 0, 0]
	let isRun = true
	formatIndex.forEach((conf, index) =>{
	  let s = conf ? str.substr(conf.index, conf[0].length) : ''
	  if (s && isRun) {
		datePart[index] = parseInt(s)
	  } else {
		isRun = false
	  }
	});
	datePart[1] -= 1
	return new Date(...datePart)
  } else {
	return ''
  }
}


function getAnchorParams (date) {
  return parseTimeAllParams(strToDate(parseTimeAllParams(date).strTime, 'yyyy-MM-dd'))
}