



import AD from "@/common/js/ad-mp-weixin.js";

export default {
	data() {
		return {
			loaded: false,
			adTimer: null
		}
	},
	watch: {
		barTitle(newVal) {
			uni.setNavigationBarTitle({
				title: newVal
			});
		},
		adConfig() {
			this.loadVideoAd();
		},
	},
	computed: {
		videoAdWaittingTime() {
			//
			return this.$store.state.app.customer.videoAdWaittingTime;
		}
	},
	onLoad() {
		//
		// this.loadVideoAd();
	},
	onUnload() {
		clearTimeout(this.adTimer)
	},
	onShow() {
		console.log("ad onShow")

		AD.destory()

	},
	methods: {
		loadVideoAd() {


























		},
		loadVideoAdByConfig(config) {





		},
		/**
		 * 
		 * @param {Object} task
		 */
		signin(task) {
			console.log("")

			return this.showRewardedVideoAd(task, "signin");

			// this.finishTask(task, "signin", {})
		},
		articleRead(task) {
			console.log("")
			return this.finishTask(task, "finish", {}, false)
		},
		view_aritcle() {
			uni.switchTab({
				url: "/pages/index/index"
			})
		},
		getDoubleScore(task, module) {
			console.log("")

			this.showRewardedVideoAd(task, "getdouble", {
				module
			}) || this.showFullScreenVideoAd(task, "getdouble", {
				module
			});

			/* this.finishTask(task, "getdouble", {
				module
			}) */
		},
		shareToFriend(task) {
			let data = this.$stringFormat(task.data, {
				my_invite_code: this.userInfo.my_invite_code
			});
			console.log("app", data)

			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				...JSON.parse(data),
				success: (res) => {
					this.finishTask(task, "share", {}, false)
					console.log("success:" + JSON.stringify(res));
				},
				fail: (err) => {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		showRewardedVideoAd(task, type = "signin", data = {}) {
			if (!task.adpid && !this.adConfig.signin) {
				this.$message("")
			}
			let config = {
				adpid: task.adpid || this.adConfig.signin, // HBuilder 
				adType: "RewardedVideo"
			}
			//  loading 
			AD.show(config, (res) => {
				this.loadVideoAdByConfig(config)
				// 
				if (res && res.isEnded || task.middle_getout) {
					// 
					this.finishTask(task, type, data)
					console.log("onClose " + res.isEnded);
				} else {
					// 
					console.log("onClose " + res.isEnded);
					uni.showToast({
						title: "",
						icon: "none"
					})
				}
			}, (err) => {
				// 
				console.log(err)
				if (err.errMsg && err.errMsg.indexOf("") != -1) {
					console.log("")
					this.showFullScreenVideoAd(task, type, data);
				} else {
					uni.showToast({
						title: err.errMsg || "",
						icon: "none"
					})
				}
			})
			return true;
		},
		showFullScreenVideoAd(task, type = "finish", data = {}) {
			if (!task.adpid && !this.adConfig.video) {
				this.$message("")
				return false;
			}
			let config = {
				adpid: task.adpid || this.adConfig.video, // HBuilder 
				adType: "FullScreenVideo"
			};
			//  loading 
			AD.show(config, (res) => {
				this.loadVideoAdByConfig(config)
				// 
				if (res && res.isEnded || task.middle_getout) {
					// 
					this.finishTask(task, type, data)
					console.log("onClose " + res.isEnded);
				} else {
					// 
					console.log("onClose " + res.isEnded);
					uni.showToast({
						title: "",
						icon: "none"
					})
				}
			}, (err) => {
				// 
				console.log(err)
				uni.showToast({
					title: err.errMsg || "",
					icon: "none"
				})
			})
			return true;
		},
		showInterstitialAd() {
			if (!this.adConfig || !this.adConfig.interstitial) {
				// this.$message("")
				return false;
			}
			let config = {
				adpid: this.adConfig.interstitial, //this.adConfig.interstitial, // HBuilder 1111111113
				adType: "Interstitial"
			};
			AD.show(config, (res) => {
				this.loadVideoAdByConfig(config)
				// 
				if (res && res.isEnded) {
					// 
					console.log("onClose " + res.isEnded);
				} else {
					// 
					console.log("onClose " + res.isEnded);
				}
			}, (err) => {
				// 
				console.log(err)
			})
			return true;
		},
		finishTask(task, type = "finish", data = {}, isVideo = true) {
			let task_log_id = "";
			if (task.log && task.log._id) {
				task_log_id = task.log._id;
			}
			this.$request('user/task/' + type, {
				data: task.data,
				...data,
				task_log_id,
				task_id: task._id
			}, {
				loading: true
			}).then(res => {
				console.log("finishTask", res)
				//amount,result
				this.$store.commit('user/UPDATE_TASK_LOG', res)
				//
				// this.loadVideoAd();
				this.showWaitAdTime(res, isVideo ? this.videoAdWaittingTime : 0);
			}, err => {
				uni.hideLoading();
			});
		},
		showWaitAdTime(res, number) {
			this.loaded = false;
			if (number > 0) {
				uni.showLoading({
					mask: true,
					title: `${number}`
				})
				this.adTimer = setTimeout(() => {
					this.showWaitAdTime(res, number - 1);
				}, 1000)
			} else {
				uni.hideLoading();
				this.loaded = true;
				this.finishTaskResult(res)
				this.buildTaskResult(res);
			}
		},
		/**
		 * ,
		 * @param {Object} res
		 */
		finishTaskResult(res) {
			if (res.getDouble) {
				this.$message(` +${res.task.bounty}`)
			} else if (res.getScore) {
				this.$message(` +${res.task.bounty}`)
			} else if (res.needMore) {
				this.$message(`${res.amount}`)
			} else if (res.result) {
				this.$message("")
			} else {
				uni.hideLoading();
			}
		},
		buildTaskResult(log) {}
	},
}
